<template>
    <div class="item">
        <div class="columns is-vcentered is-mobile is-variable is-1-mobile">
            <div class="column">
                <div class="select is-fullwidth">
                    <select v-model="unit1">
                        <option v-for="(option, index) in options" :key="index" :value="index">{{option.name}}</option>
                    </select>
                </div>
            </div>
            <div class="column">
                <div class="select is-fullwidth">
                    <select v-model="unit2">
                        <option v-for="(option, index) in options" :key="index" :value="index">{{option.name}}</option>
                    </select>
                </div>
            </div>
            <div class="column is-narrow">
                <span class="dummy"></span>
            </div>
        </div>
    </div>

    <form @input="result">
        <ul>
            <li v-for="(field, index) in fields" :key="index" class="item" :class="{'has-background-danger-light':(field === highestEl && field != lowestEl ), 'has-background-primary':(field != highestEl && field === lowestEl)}">
                <div class="columns is-mobile is-variable is-1-mobile">
                    <div class="column">
                        <div class="field">
                            <div class="control">
                                <input class="input" type="number" v-model.number="field.n1" @input="result" inputmode="numeric" pattern="[0-9]*" placeholder="0.00" min="1" step="any">
                                <small>1{{options[unit2].unit}} = {{field.ptom}} {{options[unit1].unit}} </small>
                            </div>
                        </div>
                    </div>
                    <div class="column">
                        <div class="field">
                            <div class="control">
                                <input class="input" type="number" v-model.number="field.n2" @input="result" inputmode="numeric" pattern="[0-9]*" placeholder="0.00" min="1" step="any">
                                <small>1{{options[unit1].unit}} = {{field.mtop}} {{options[unit2].unit}}</small>
                            </div>
                        </div>
                    </div>
                    <div class="column is-narrow">
                        <div class="field">
                            <button tabindex="-1" class="button is-danger" @click.prevent="removeField(index)">
                                <span class="icon">
                                    <span class="material-icons">close</span>
                                </span>
                            </button>
                        </div>
                    </div>
                </div>
            </li>
        </ul>
    </form>
    <div class="is-fab">
        <button class="button is-rounded is-primary" @click.prevent="addField()">
            <span class="icon"><span class="material-icons">add</span></span>
        </button>
    </div>
</template>

<script>
export default {
    data() {
        return {
            unit1: 0, // Set default values
            unit2: 0,
            options: this.appOptions,
            fields: [
                {
                    n1: "",
                    n2: "",
                    res: 0,
                    mbp: 0,
                    ptom: 0.00,
                    mtop: 0.00,
                },
                {
                    n1: "",
                    n2: "",
                    res: 0,
                    mbp: 0,
                    ptom: 0.00,
                    mtop: 0.00,
                }
            ]
        };
    },
    computed: {
        result() {
            let fieldList = this.fields;
            fieldList.forEach(field => {
                let val = field.n2 / field.n1;
                let val2 = field.n1 / field.n2;

                if (isNaN(val)) {
                    field.res = "--";
                    field.mtop = "--";
                } else {
                    if (val === Infinity || val === -Infinity) {
                        field.res = "Error";
                    } else {
                        field.res = val;
                        //field.mtop = val.toFixed(2);
                        field.mtop = val.toFixed(Math.max(2, val.toString().split('.')[1]?.length || 0));
                    }
                }

                if (isNaN(val2)) {
                    field.mbp = "--";
                    field.ptom = "--";
                } else {
                    if (val2 === Infinity || val2 === -Infinity) {
                        field.mbp = "Error";
                    } else {
                        field.mbp = val2;
                        //field.ptom = val2.toFixed(2);
                        field.ptom = val2.toFixed(Math.max(2, val2.toString().split('.')[1]?.length || 0));
                    }
                }
            });
        },
        highestEl() {
            if (this.fields.length === 0) {
                return;
            }
            return this.fields.reduce((a, b) => Number(a.res) > Number(b.res) ? a : b);
        },
        lowestEl() {
            if (this.fields.length === 0) {
                return;
            }
            return this.fields.reduce((a, b) => Number(a.res) < Number(b.res) ? a : b);
        }
    },
    methods: {
        addField() {
            this.fields.push({
                n1: "",
                n2: "",
                res: 0,
                mbp: 0,
                ptom: 0.00,
                mtop: 0.00,
            });
        },
        removeField(index) {
            this.fields.splice(index, 1);
            if (this.fields.length <= 1) {
                this.addField();
            }
        }
    }
}
</script>

